/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box; }

img {
  display: block; }

.btn {
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder; }
  .btn:link, .btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 1.2rem 3rem;
    border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--accent {
    background-color: #fd8d5c;
    color: #fff; }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.featured-pets {
  padding: 3rem 8rem 5rem; }
  .featured-pets h1 {
    color: #fd8d5c; }
  .featured-pets hr {
    background-color: #1c75bb; }
  .featured-pets .pet-outer {
    display: flex;
    align-items: flex-end; }
  .featured-pets .pet-info {
    flex: 1 0;
    margin-right: 1.5rem; }
    .featured-pets .pet-info h2 {
      color: #a0cd3a;
      font-size: 3.3rem; }
    .featured-pets .pet-info ul {
      list-style: none;
      margin-top: 0.5rem; }
      .featured-pets .pet-info ul li {
        display: flex;
        align-items: center;
        padding: 0.3rem 0;
        border-top: solid 1px #1c75bb; }
        .featured-pets .pet-info ul li:last-child {
          border-bottom: solid 1px #1c75bb; }
        .featured-pets .pet-info ul li label {
          flex: 1;
          font-size: 2rem;
          font-weight: 500; }
        .featured-pets .pet-info ul li span {
          flex: 2;
          font-size: 1.5rem; }
  .featured-pets .pet-image img {
    max-height: 24.5rem; }
  .featured-pets .btn-container {
    margin-top: 4rem;
    text-align: center; }
    .featured-pets .btn-container a {
      margin: 1rem; }
