/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box; }

img {
  display: block; }

.btn {
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder; }
  .btn:link, .btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 1.2rem 3rem;
    border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--accent {
    background-color: #fd8d5c;
    color: #fff; }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.events h2 {
  font-size: 3.5rem;
  color: #1c75bb;
  margin-top: 5rem; }

.events a {
  color: #fd8d5c;
  font-weight: 700; }

.events img {
  width: 27rem;
  margin-left: 1rem; }

.events .right {
  float: right; }

.events ul {
  margin-left: 2rem; }

.events li {
  font-size: 1.8rem;
  margin: 3rem 0; }

.events .dogs {
  display: flex;
  align-items: center; }
  .events .dogs > div {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    border: 0.6rem solid #1c75bb;
    border-radius: 1.5rem;
    margin-right: 1rem;
    padding: 3rem; }
    .events .dogs > div h2 {
      margin: 0; }
    .events .dogs > div h3 {
      font-size: 2.5rem;
      color: #1c75bb; }
    .events .dogs > div p {
      margin: 2rem 0 0 0; }
