/* CSS Reset */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  box-sizing: border-box; }

img {
  display: block; }

.btn {
  font-family: 'Raleway', sans-serif;
  font-size: 1.4rem;
  font-weight: bolder; }
  .btn:link, .btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 1.2rem 3rem;
    border-radius: 1.7rem; }
  .btn:hover {
    transform: translateY(-0.3rem); }
  .btn:active {
    transform: translateY(-0.1rem); }
  .btn--accent {
    background-color: #fd8d5c;
    color: #fff; }
  .btn--secondary {
    background-color: #a0cd3a;
    color: #fff; }

hr {
  background-color: #fff;
  margin: .5rem 0 2rem;
  width: 4rem;
  height: 0.3rem;
  border: 0; }

.donate {
  font-family: 'Raleway', sans-serif;
  padding: 5rem 14rem 0 10rem; }
  .donate .main {
    flex: 1 0; }
  .donate .top {
    position: relative; }
    .donate .top img {
      width: 100%; }
    .donate .top h1 {
      position: absolute;
      top: 1.5rem;
      left: 1.5rem;
      color: greenyellow;
      font-size: 3.5rem; }
    .donate .top p {
      position: absolute;
      bottom: 0.6rem;
      right: 0.6rem;
      color: #fff;
      width: 29rem;
      font-weight: 700;
      font-size: 1.4rem; }
  .donate .make-donation {
    display: flex;
    justify-content: space-evenly;
    margin: 4rem 0 2.5rem; }
    .donate .make-donation > div {
      position: relative;
      z-index: 100; }
      .donate .make-donation > div img {
        position: absolute;
        left: 50%;
        top: -1.5rem;
        transform: translateX(-50%);
        height: 11rem; }
      .donate .make-donation > div > div {
        margin-top: 4rem;
        border: 0.6rem solid #1c75bb;
        border-radius: 1.5rem;
        display: flex;
        flex-direction: column;
        padding: 7rem 2rem 3rem;
        color: #a0cd3a;
        z-index: 50; }
      .donate .make-donation > div p {
        font-size: 3.5rem;
        font-weight: 700;
        line-height: 3.5rem;
        text-align: center; }
    .donate .make-donation .btn-container {
      text-align: center;
      margin-top: 4rem; }
  .donate .one-time {
    margin-right: 2rem; }
  .donate .mail-check {
    background-color: #a0cd3a;
    padding: 2rem; }
    .donate .mail-check p {
      color: #fff;
      font-size: 1.8rem;
      font-weight: 700;
      text-align: center; }
  .donate .other {
    background-image: url("../assets/images/largeBlueCat.png");
    padding: 2rem 6rem 3rem; }
    .donate .other h1 {
      font-size: 3.5rem;
      color: #fd8d5c; }
    .donate .other hr {
      background-color: #fff; }
    .donate .other .panels {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap; }
    .donate .other .panel {
      background-color: white;
      border-radius: 1.5rem;
      margin-bottom: 5rem;
      padding: 2rem;
      width: 30%;
      min-height: 17rem;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center; }
      .donate .other .panel > p {
        color: #1c75bb;
        margin-bottom: 2rem;
        font-size: 2.9rem;
        font-weight: 700;
        text-align: center; }
